import React from "react"
import { StaticQuery, graphql, Link } from "gatsby"


const menuQuery = graphql`{
  allWordpressWpApiMenusMenusItems {
      edges {
        node {
          name
          items {
            title
            url
            object_slug
            object
          }
        }
      }
    },
    site {
      siteMetadata {
        siteURL
      }
    }
    allWordpressWpMedia {
      edges {
        node {
          slug
          source_url
        }
      }
    }
  }  
`;


